import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;
import { GlobalValidate } from '../../../../_services/validate.service';

const NUM_REGEX=/[0-9]/;
const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./campaign-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})
export class CampaignAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
@ViewChild(ToastComponent) toast: ToastComponent;

status;	
campaignForm: FormGroup;
campaignId = 0;  
campaign = new campaignData();
 
msg = [];
bootstrapFormValues = [];
campaigntype = [];
branch =[];
branchIds=[];
branchSelected=[];
 
bootstrapMsg='';
campaignstartdatemsg:string;
campaignenddatemsg:string;
 
constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute, private globalValid: GlobalValidate) {
	this.route.queryParams.filter(params => params.campaignId).subscribe(params=>{
			if(params.campaignId != undefined){
				this.campaignId=params.campaignId; 
			}
		}); 
	if(this.campaignId == 0){
		this.populateDropDownData();
		this.status = "Add";
	}else{
		this.populateData();
		this.status = "Update";
	}
	}
	
     ngOnInit() {
		
		this.campaignForm = new FormGroup({	
			'campaignCampaigntypeId' : new FormControl('',[ Validators.required] ),
			'campaignName' : new FormControl('',[ Validators.required] ),
			'campaignDesc' : new FormControl('',[ Validators.required] ), 
//			'campaignStartdate' : new FormControl(''), 
//			'campaignEnddate' : new FormControl(''), 
			'campaignBudget' : new FormControl('',[ Validators.pattern(NUM_REGEX)]),
			'campaignActive' : new FormControl(''),
			'campaignNotes' : new FormControl(''),
			'branchName' : new FormControl(''),
			  		
		});
		 
				}
	  
	  get campaignCampaigntypeId(){ return this.campaignForm.get('campaignCampaigntypeId')};
	  get campaignName(){ return this.campaignForm.get('campaignName')};
	  get campaignDesc(){ return this.campaignForm.get('campaignDesc')};
//	  get campaignStartdate(){ return this.campaignForm.get('campaignStartdate')};
//	  get campaignEnddate(){ return this.campaignForm.get('campaignEnddate')};
	  get campaignBudget(){ return this.campaignForm.get('campaignBudget')};
	  get campaignActive(){ return this.campaignForm.get('campaignActive')}; 
	  get campaignNotes(){ return this.campaignForm.get('campaignNotes')}; 
	  get branchName(){ return this.campaignForm.get('branchName')}; 
	
 
 	populateDropDownData(){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/campaigntype',
								 JSON.stringify({ campaignId :this.campaignId}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.campaigntype = data['populateCampaignType']
																	}); 
		
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/campaignbranch',
							 			JSON.stringify({ }),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.branch = data['populateCampaignBranch']; 
			  															});
		
//		 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/campaignbranchselected',
//							 			JSON.stringify({ }),
//									{ headers, withCredentials: true }).subscribe(data =>{
//				  														this.branchSelected = data['populateCampaignBranchSelected']; 
//			  															});
		
	 }
 
	  populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			   this.http.post<campaignData>(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/campaign-data',
							 			JSON.stringify({ campaignId :this.campaignId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.campaign = data['populateData'][0];
			  															},
															 (err) => {
																	console.log("ERROR: "+err);
															 },
															 () => {
			  														this.populateDropDownData();
				   
			this.http.post<campaignData>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/campaignbranch',JSON.stringify({'branchIds':this.campaign.branchId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.branchSelected =data['populateCampaignBranch'];
																		
			  															},
															 (err) => {
																	console.log("ERROR: "+err);
															 },
															 () => {
															 });
				   
				   
															 });
	 } 
	  
 
 		AddData(){
		var $srcOption = $(".firstSelect option:checked").clone();
		var $destOption = $(".secondSelect option").clone();
		var duplicate = 0; 
 
		 $($srcOption).each(function(){ 
			 var source = this;
			 if($(".secondSelect option").length)
			 { 
				 $($destOption).each(function(){ 
				 var dest = this;
					if(dest.innerHTML == source.innerHTML)
						duplicate = 1; 
				 });
				 if(duplicate.toString() == "0") 
						$('.secondSelect').prepend(source);
					 duplicate = 0;
			 }
			 else 
				$('.secondSelect').prepend($srcOption);
			});
		 $(".secondSelect option").prop('selected',true);
		}
	  
	  
		DeleteData(){
			$(".secondSelect option:checked").remove();
		}
 
 
 
	   ngAfterViewInit(){
		  
		  $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
		}	
	
 
		 toggleAnimation(){
				   this._router.navigate(['sales/campaignlist']);
		 }

 	getCheckValues(temp:string){
	return this.globalValid.getCheckValue(temp);
	
	}
 
 
	  	onSubmit() {
	  	$(".secondSelect option").prop('selected',true);
	  	var j=0;
		this.bootstrapFormValues = $('#m_form_1').serializeArray();
		for(var i=0;i<this.bootstrapFormValues.length;i++){
			if(this.bootstrapFormValues[i].name != 'branchName'){
				if(this.bootstrapFormValues[i].name.includes('check_')){
					this.campaign[this.bootstrapFormValues[i].name.split('_')[1]] = "1";
				}else{
					console.log(this.bootstrapFormValues[i].name+"name============");
					console.log(this.bootstrapFormValues[i].value+"value============");
					this.campaign[this.bootstrapFormValues[i].name] = this.bootstrapFormValues[i].value;
				}
			}else{
				this.branchIds[j]=this.bootstrapFormValues[i].value;
				j++;
			}
			
		
		this.branchIds['branchName'] = this.branchIds;
				
			//	this.campaign[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
			if(this.bootstrapFormValues[i].name=="campaignStartdate"){
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter Start Date!<br>';
						this.campaignstartdatemsg="<font class='errorMsg'>Enter Start Date</font>";
						
					}else{
						this.campaignstartdatemsg = "";
					}
				}
			
			if(this.bootstrapFormValues[i].name=="campaignEnddate"){
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter End Date!<br>';
						this.campaignenddatemsg="<font class='errorMsg'>Enter End Date</font>";
						
					}else{
						this.campaignenddatemsg = "";
					}
				}
		}
			
			if(this.bootstrapMsg != ''){
				this.msg[0]='Error';
			 	this.msg[1]='Enter the Mandatory Fields' ;
				this.toast.showtoast(this.msg);
				this.bootstrapMsg='';
			}
			this.campaign['branchName'] = this.branchIds;
			console.log(this.campaign);
		  if(!this.campaignForm.invalid && this.bootstrapMsg == ''){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
			  
			if(this.campaignId == 0){ 
				this.campaign['requestType'] = 'add';
			}else{
				this.campaign['requestType'] = 'update';
				this.campaign['campaignId'] = this.campaignId+'';
			}
		 	this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/campaign',
									JSON.stringify(this.campaign),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'] == undefined){
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}else{
														if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}
														}
			  											this.toast.showtoast(this.msg);
			  											if(this.msg[0]!='Error'){
			 											this.campaignForm.reset();
														this.toggleAnimation();
														}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  }
	  }

 
 		onDelete(){
		 alert(this.campaignId);
		 this.campaign['requestType'] = 'delete';
		 this.campaign['campaignId'] = this.campaignId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/campaign',
									JSON.stringify(this.campaign),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'] == undefined){
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}else{
														if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}
														}
			  											this.toast.showtoast(this.msg);
			  											if(this.msg[0]!='Error'){
			 											this.campaignForm.reset();
														this.toggleAnimation();
														}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		 
	 		}

}


export class campaignData { 
	campaignId: string;
	campaignCampaigntypeId: string;
	campaignName: string;
	campaignDesc: string;
	campaignStartdate : string='';
	campaignEnddate: string='';
	campaignBudget: string;
	campaignActive: string = "1";
	campaignNotes: string; 
	branchName: string[];
	branchId: string='';
	campaignEntryId:string;
	campaignEntryDate:string;
	campaignModifiedId:string;
	campaignModifiedDate:string;
}
	  
	 


